@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@400;500;600;700;800&display=swap);
*{padding:0;margin:0;box-sizing:border-box}html,body{width:100%;height:100vh;font-family:'Exo 2', sans-serif}.container{text-align:center}.container img{width:400px;margin:50px 0 30px}.container .border,.container .search-box input,.container .search-box button,.container .search-box .recipe-container .recipe-card .btn a,.container .recipe-container .recipe-card .btn .search-box a{font-weight:600;border-radius:25px;font-size:1rem}.container .search-box{width:800px;height:50px;display:flex;justify-content:space-between;margin:0 auto}.container .search-box input{flex-basis:70%;padding:0 15px;width:100%;border:2px solid #2C3B8D;color:#2C3B8D;background:rgba(0,0,0,0)}.container .search-box input:focus{outline:none}.container .search-box button,.container .search-box .recipe-container .recipe-card .btn a,.container .recipe-container .recipe-card .btn .search-box a{width:20%;cursor:pointer;width:20%;border:none;color:#fff;background:linear-gradient(to right, #2C3B8D, #04A3E5)}.container .recipe-container{display:flex;flex-wrap:wrap;justify-content:space-evenly}.container .recipe-container .recipe-card{width:300px;box-shadow:2px 2px 10px #2C3B8D;border-radius:20px;padding:10px;color:#2C3B8D;margin:40px 20px}.container .recipe-container .recipe-card img{width:100%;margin:0;border-radius:10px}.container .recipe-container .recipe-card .btn{display:flex;justify-content:space-evenly;margin:10px 0}.container .recipe-container .recipe-card .btn button,.container .recipe-container .recipe-card .btn a{background:linear-gradient(to left, #2C3B8D, #04A3E5);color:#fff;border-radius:10px;padding:10px 20px;border:none;text-transform:capitalize;font-weight:600;cursor:pointer;font-size:1rem}.container .recipe-container .recipe-card .btn a{text-decoration:none}

